$path_images: '../'
$background: #CDE4FF
$gray: #C9C9C9
$error: #c28f8f

#loginForm
  height: 100vh
  display: grid
  grid-template-columns: 12fr 3fr
  background-color: #CDE4FF
  .left
    background: url("../../../public/images/wotch.png") no-repeat
    width: 100%
  .right
    display: flex
    background: #ffffff
    justify-content: center
    align-items: center
    flex-direction: column
    .title
      font-style: normal
      font-weight: 400
      font-size: 35px
      line-height: 100%
    .input-group
      display: flex
      flex-direction: column
      margin-top: 50px
      transition: .4s
      .input
        height: 40px
        width: 270px
        margin: 5px 0
        padding: 16px 10px
        background: #ffffff
        border: 2px solid $gray
        box-sizing: border-box
        border-radius: 4px
        line-height: 40px
        display: flex
        align-items: center
        transition: .4s
        &::placeholder
          color: $gray
        &.error
          border: 2px solid $error
          color: $error
          &::placeholder
            color: $error
      .button
        height: 40px
        background: #76A2DD
        border: 1px solid #54739c
        box-sizing: border-box
        border-radius: 2px
        color: #ffffff
        width: 50%
        align-self: center
        margin-top: 20px
        transition: .15s
        line-height: 40px
        padding: 0 5px
        cursor: pointer
        &:hover
          border-radius: 20px
      .block-error
        margin: 10px 0
        color: $error
        text-align: center
        max-width: 270px
.input-pass
  position: relative
  .btn-show-pass
    display: flex
    width: 15px
    height: 10px
    position: absolute
    background-color: #76A2DD
    right: 10px
    top: 20px
    justify-content: center
    align-items: center
    border-radius: 10px
    &::after
      display: block
      content: ""
      height: 2px
      width: 2px
      background-color: #54739c
      border-radius: 10px
    &::before
      transition: .5s
      opacity: 0
      content: ""
      position: absolute
      width: 20px
      height: 2px
      background-color: #c28f8f
      border-radius: 10px
  .btn-show-pass.show
    &::before
      opacity: 1
      transform: rotate(45deg)



