$background: #ffcdbe9c

.header
  background-color: $background
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25)
  position: absolute
  display: flex
  align-items: center
  justify-content: space-between
  height: 65px
  width: 100%
  left: 0
  top: 0
  padding: 0 20px
  &.relative
    position: relative
  .logo
    font-style: normal
    font-weight: 400
    font-size: 24px
    line-height: 100%
    max-width: 150px
  .logout
    display: flex
    align-items: center
    height: 100%

