$gray: #C9C9C9
$error: #c28f8f

.inputComponent, .selectComponent
  height: 40px
  min-width: 100px
  width: 100%
  margin: 5px 0
  padding: 16px 10px
  background: #ffffff
  border: 2px solid $gray
  box-sizing: border-box
  border-radius: 4px
  line-height: 40px
  display: flex
  align-items: center
  &::placeholder
    color: $gray
  &.error
    border: 2px solid $error
    color: $error
    &::placeholder
      color: $error

.selectComponent
  padding: 0 10px
