$background: rgba(0, 0, 0, 0.61)
$backgroundModal: rgba(255, 255, 255, 1)

.modal
  position: fixed
  display: flex
  justify-content: center
  align-items: center
  width: 100vw
  height: 100vh
  z-index: 101
  left: 0
  top: 0
  .background
    width: 100vw
    height: 100vh
    position: absolute
    background-color: $background
    animation: showBackground .3s ease-in-out
  .modal-container
    display: flex
    flex-direction: column
    justify-content: space-between
    width: 800px
    min-height: 500px
    max-height: 90vh
    z-index: 102
    background-color: $backgroundModal
    border-radius: 10px
    position: relative
    overflow: scroll
    animation: showModal .3s ease-in-out
    border: 1px solid #c28f8f
    max-width: 1200px
    .modal-body
      width: 100%
      position: relative
      overflow: scroll
      padding: 50px 20px 20px 20px
      height: 100%
      &::-webkit-scrollbar
        display: none
    .tool-top-panel
      position: absolute
      height: 40px
      width: 100%
      background-color: rgba(118, 162, 221, 0.6)
      display: flex
      top: 0
      padding: 0 20px
      z-index: 110
      justify-content: space-between
      align-items: center
      .tool-top-panel-item
        color: white
      .close
        width: 30px
        height: 30px
        color: white
        display: flex
        align-items: center
        border-radius: 100%
        justify-self: flex-end
        justify-content: center
        background-color: #c28f8f
        transition: .3s
        &:hover
          transform: rotate(360deg)



@keyframes showBackground
  from
    opacity: 0
  to
    opacity: 1


@keyframes showModal
  from
    top: -100px
  to
    top: 0

