@import "components/reset"
@import url('https://fonts.googleapis.com/css2?family=Aldrich&display=swap')

#root
  width: 100%
  min-height: 100vh
  .main
    position: relative
    display: flex
    width: 100%
    height: calc(100vh - 65px)
    max-height: calc(100vh - 65px)
    .mainBody
      margin: 40px 30px 30px 30px
      background-color: rgba(234, 234, 234, 0.96)
      padding: 20px
      border-radius: 10px
      position: absolute
      right: 0
      left: 0
      width: auto
      min-height: 95%
    &::before
      display: flex
      position: fixed
      content: ''
      background-color: #c28f8f
      width: calc(100% - 280px)
      height: 130px
      border-radius: 0 0 200px 200px
      z-index: -3
      top: 65px
    .container
      margin-top: 80px
      width: 100%
      max-width: 1000px
    .containerFluid
      margin-top: 80px
      width: 100%
      max-width: unset
      padding: 0 20px

.mainLoading
  width: 100%
  height: 100vh
  display: flex
  justify-content: center
  align-items: center

.dropMenu
  min-width: 150px
  margin-top: 10px
  opacity: 0
  z-index: -1
  position: absolute
  left: -50px
  max-height: 200px
  background-color: rgba(242, 242, 242, 0.62)
  box-shadow: 0 0 15px 0 rgba(34, 60, 80, 0.2)
  transition: .2s
  padding: 10px 5px
  .itemMenu
    border-bottom: 1px solid #C9C9C9
    padding: 10px 20px
    cursor: pointer
    &:hover
      background-color: #ffcdbe9c

.RouterSidebar
  display: flex
  width: 100%
  Router
    width: 100%
    height: 100%


