$background: #76A2DD
$mainName: #dce1e8

.sidebar
  background-color: $background
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25)
  height: calc(100vh - 65px)
  max-height: calc(100vh - 63px)
  width: 20%
  max-width: 480px
  padding: 10px
  .item
    display: flex
    width: 100%
    height: 40px
    padding: 10px
    align-items: center
    background-color: #618ec9
    margin-bottom: 5px
    border-radius: 5px
    cursor: pointer
    .icon
      width: 30px
      border-right: 1px solid #CDE4FF
      display: flex
      align-items: center
      color: $mainName
      justify-content: center
      padding-right: 10px
    .name
      display: flex
      align-items: center
      color: $mainName
      padding-left: 10px
    &.active, &:hover
      background-color: #4b76af
