.buttonComponent
    height: 40px
    background: #76A2DD
    border: 1px solid #54739c
    box-sizing: border-box
    border-radius: 2px
    color: #ffffff
    align-self: center
    transition: .15s
    line-height: 40px
    padding: 0 15px
    cursor: pointer
    &:hover
      border-radius: 20px
.buttonComponent.delete
  background: #dd7676
  border: 1px solid #9c5454

.buttonComponent.success
  background: #89dd76
  border: 1px solid #649c54

.buttonComponent.disabled
  opacity: 0.5
  cursor: not-allowed
  &:hover
    border-radius: 2px
