$backgroundBannerItem: #fafafa
$colorElements: #ffcdbe9c
$headListBackground: #e3e3e3

.design
  display: flex
  flex-direction: column
  width: 100%
  min-height: 80vh
  .designContainer
    margin-top: 20px
    border-top: 1px solid #76A2DD
    padding-top: 30px
    height: 80vh
    .designList
      display: grid
      grid-template-columns: repeat(4, 1fr)
      grid-column-gap: 20px
      grid-row-gap: 20px
      .designItemContainer
        width: 100%
        height: 300px
        .title
          text-align: center
          margin-top: 10px
          color: black
        .previewContainer
          width: 100%
          height: 250px
          overflow: hidden
          border-radius: 10px
          position: relative
          background-color: $backgroundBannerItem
          box-shadow: 0 0 10px -6px black
          .preview
            width: 130%
            max-width: 130%
            margin-left: -15%
            margin-top: -30%
          .tools
            top: 0
            position: absolute
            width: 100%
            height: 100%
            opacity: 0
            background: rgba(0, 0, 0, 0.32)
            display: flex
            align-items: center
            justify-content: center
            transition: .3s
            .toolsItem
              width: 30px
              height: 30px
              display: flex
              justify-content: center
              align-items: center
            &:hover
              opacity: 1
  .main-info
    margin-top: 20px
    .inputGroup
      display: grid
      grid-template-columns: 1fr 1fr
      grid-column-gap: 20px
      grid-row-gap: 20px
      .mainInput
        display: block
  .modalFooter
    display: flex
    align-items: flex-end
    justify-content: flex-end
    margin-top: 30px
    height: 65px



